import React from 'react';
import { Link, graphql } from 'gatsby';
import TAG_DICTIONARY from '../../content/tagDictionary.json';

import Layout from '../components/layout';
import SEO from '../components/seo';
import Subtitle from '../components/subtitle';
import { Heading, Pane, Text } from 'evergreen-ui';

const firstUpper = (word = '') => word[0].toUpperCase() + word.slice(1);

export default ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.siteTitle;
  const tags = data.allMarkdownRemark.group;
  return (
    <Layout location={location} title={siteTitle}>
      <Subtitle title="Tags" sub="See the topics of available blog here ðŸ§©" />
      <SEO title="Tags" />
      <Pane
        display="flex"
        flexDirection="column"
        paddingBottom="16px"
        paddingLeft="16px"
        paddingRight="16px"
      >
        {tags.map(({ tag, totalCount, nodes }) => (
          <Pane marginBottom="16px">
            <Pane
              display="flex"
              alignItems="center"
              flexDirection="row"
              flexWrap="wrap"
            >
              <Heading size={600}>{`${firstUpper(tag)} >`} </Heading>
              <Text color="muted">{TAG_DICTIONARY[tag.toLowerCase()]}</Text>
            </Pane>
            <Pane marginLeft="16px" flexDirection="column" display="flex">
              {nodes.map(({ fields: { slug }, frontmatter }) => (
                <Link to={slug}>
                  <Text color="#429777" size={500}>
                    {frontmatter.title}
                  </Text>{' '}
                </Link>
              ))}
            </Pane>
          </Pane>
        ))}
      </Pane>
    </Layout>
  );
};

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark {
      group(field: frontmatter___tags) {
        tag: fieldValue
        totalCount
        nodes {
          frontmatter {
            title
          }
          fields {
            slug
          }
        }
      }
    }
  }
`;
